.menu {
  display: flex;
  justify-content: space-between;
  list-style: none;
  background: #2b3f52;
  border: 1px solid #2F80ED;
  box-sizing: border-box;
  border-radius: 8px;
}

.menu__link {
  font-weight: 500;
  line-height: 20px;
  font-size: 14px;
  letter-spacing: -0.380455px;
  color: $white;
  text-decoration: none;
  padding-left: 35px;
  display: inline-block;
  transition-duration: 500ms;
  transition-property: color;
  border-bottom: 3px solid transparent;
  padding-top: 15px;
  padding-bottom: 12px;

  &:hover {
    border-bottom-color: #5683FF;
  }

}


.menu__element {

  &:nth-child(1) {

    .menu__link {
      background: url('../img/compact-disc.png') left center no-repeat;
    }

  }

  &:nth-child(2) {

    .menu__link {
      background: url('../img/support.png') left center no-repeat;
    }

  }

  &:nth-child(3) {

    .menu__link {
      background: url('../img/support-2.png') left center no-repeat;
    }

  }

}